import router from './router/router'
import store from './store'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import { getToken } from './utils/auth'

NProgress.configure({ showSpinner: false }) // NProgress 的配置

const whiteList = ['/login', '/tabletMove'] // 定义不需要登录就能访问的页面

/* 在路由进行跳转前进行权限和用户信息等处理 */
router.beforeEach((to, from, next) => {
  NProgress.start() // 出现跳转进度条
  let arr = []
  if (store.getters.getloginInfor() !== null) {
    if (store.getters.getloginInfor().resource !== undefined) {
      for (let item of store.getters.getloginInfor().resource) {
        arr.push(item.id)
      }
    }
  }
  if (getToken() && store.getters.getloginInfor() !== null) {
    /* 已经有token */
    next()
  } else {
    /* 在没有token的时候 */
    if (whiteList.includes(to.path)) {
      /* 在免登录白名单中，直接进入 */
      next()
    } else {
      next(`/login?redirect=${to.path}`) // 否则全部重定向到登录页
    }
  }
  if (to.meta.id !== undefined) {
    if (arr.indexOf(to.meta.id) === -1) {
      next(`/403`) // 否则全部重定向到403
    } else {
      next()
    }
  }
})

router.afterEach(() => {
  NProgress.done() // 结束进度条
})
